.FI-body-wrap {
    .lohp-row {
        margin-bottom: 48px;
        padding: 0px 32px;

        @media screen and (max-width: 900px) {
            margin-bottom: 96px;
        }

        @media screen and (max-width: 600px) {
            margin-bottom: 80px;
        }

    }

    .FI-hero-wrap {
        height: 680px;
        transition: all 0.5s ease;
        position: relative;

        @media (max-width: 900px) {
            height: 415px;
        }

        .FI-hero-bg {
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            animation: fade 2s linear;

            img {
                width: 100%;
                height: 100%;
            }

            @media (max-width: 900px) {
                display: none;
            }
        }

        .FI-hero-content-wrap {
            height: 100%;
            z-index: 1;

            .FI-hero-content {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                justify-items: center;
                height: 100%;
                padding: 0px 24px;
                z-index: 1;

                .hero-content-header {
                    color: white;
                    padding-bottom: 24px;
                    font-size: 40px;
                    line-height: 48px;
                    font-family: Macan, Helvetica Neue, Helvetica, Arial, sans-serif;
                    font-weight: 700;
                    z-index: 1;

                    @media (max-width: 600px) {
                        font-size: 32px;
                        line-height: 40px;
                    }
                }

                .hero-search-section {
                    display: flex;
                    flex-direction: row;
                    width: 100%;

                    .search-input {
                        flex: 1;
                        position: relative;
                        z-index: 1;

                        input {
                            width: 100%;
                            height: 50px;
                            border: 0px;
                            border-radius: 4px 0px 0px 4px;
                            margin-bottom: 0;
                            font-size: 16px;
                            font-weight: 500;
                            padding: 0px 20px 0px 45px;
                            z-index: 1;

                            &:focus-visible {
                                outline: 0px;
                            }
                        }

                        .search-icon {
                            position: absolute;
                            display: flex;
                            top: 0px;
                            height: 100%;
                            width: 45px;
                            align-items: center;
                            justify-content: center;
                            justify-items: center;
                            z-index: 1;

                            svg {
                                fill: #74767e;
                                font-size: 23px;
                            }
                        }

                        .cancel-icon {
                            position: absolute;
                            display: none;
                            right: 0px;
                            top: 0px;
                            height: 100%;
                            width: 20px;
                            align-items: center;
                            justify-content: center;
                            justify-items: center;
                            z-index: 1;

                            svg {
                                font-size: 16px;
                                fill: #395f9f;
                                cursor: pointer;
                            }
                        }

                        &:hover {
                            .cancel-icon {
                                display: flex;
                            }
                        }
                    }

                    .search-btn {
                        height: 50px;
                        border-radius: 0 4px 4px 0;
                        font-size: 16px;
                        padding: 0px 24px;
                        color: white;
                        font-weight: bold;
                        background-color: #1dbf73;
                        display: flex;
                        align-items: center;
                        text-align: center;
                        justify-content: center;
                        justify-items: center;
                        cursor: pointer;
                        z-index: 1;

                        &:hover {
                            background-color: #19a463;
                        }
                    }

                    @media (max-width: 900px) {
                        flex-direction: column;

                        .search-input {
                            width: 100%;

                            input {
                                border-radius: 4px;
                            }
                        }

                        .search-btn {
                            width: 100%;
                            border-radius: 4px;
                            margin-top: 12px;
                        }
                    }
                }

                .here-footer-sec {
                    margin-top: 24px;
                    width: 100%;
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    z-index: 1;

                    .footer-content {
                        font-size: 14px;
                        line-height: 27px;
                        font-weight: 600;
                        color: #fff;
                        white-space: nowrap;
                    }

                    .footer-items {
                        flex: 1;
                        display: flex;
                        margin-left: 12px;

                        .footer-item {
                            margin-right: 12px;
                            white-space: nowrap;

                            a {
                                text-decoration: none;
                                font-size: 14px;
                                line-height: 24px;
                                font-weight: 600;
                                color: #fff;
                                padding: 6px 12px;
                                border: 1px solid #fff;
                                border-radius: 40px;
                                transition: all .2s ease-in-out;

                                &:hover {
                                    background-color: #fff;
                                    color: #404145;
                                }
                            }
                        }
                    }

                    @media (max-width: 900px) {
                        display: none;
                    }
                }
            }

            @media (min-width: 600px) {
                padding: 0px 32px;

                .FI-hero-content {
                    padding: 0px 64px;
                }
            }

            @media (min-width: 900px) {
                .FI-hero-content {
                    width: 560px;
                    padding: 0px;
                }
            }
        }
    }

    .trusted-by-wrapper {
        height: 90px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        white-space: nowrap;

        .trusted-text {
            padding-right: 20px;
            color: #b5b6ba;
            font-weight: 600;
            display: flex;
            align-items: center;
        }

        .trusted-items-wrap {
            display: flex;
            white-space: nowrap;
            padding: 0px 24px;

            .trusted-item {
                padding-right: 24px;
            }
        }


        @media screen and (max-width: 900px) {
            height: 70px;
        }
    }

    .subcategory-carousel-wrap {
        .subcategory-text {
            padding-bottom: 20px;
            font-size: 32px;
            line-height: 120%;
            color: #404145;
            font-family: Macan, Helvetica Neue, Helvetica, Arial, sans-serif;
            font-weight: 700;
        }

        .slider-wrap {
            .slider-containner {
                .react-slideshow-container {
                    .default-nav {
                        &:first-of-type {
                            margin-right: -15px;
                        }

                        &:last-of-type {
                            margin-left: -15px;
                        }
                    }
                }

                .Slider_items-group {
                    height: 345px;
                    display: flex;

                    .slider-item {
                        display: flex;
                        margin: 0px 9px;
                        width: 100%;
                        height: 100%;
                        position: relative;

                        a {
                            text-decoration: none;
                            width: 100%;
                            height: 100%;
                        }

                        img {
                            width: 100%;
                            height: 100%;
                            position: absolute;
                            z-index: -1;
                        }

                        .slider-content {
                            z-index: 1;
                            color: #fff;
                            padding: 16px;

                            .content-header {
                                font-size: 14px;
                                line-height: 20px;
                                font-weight: 400;
                                display: block;
                            }

                            .content-title {
                                font-size: 24px;
                                line-height: 130%;
                                font-family: Macan, Helvetica Neue, Helvetica, Arial, sans-serif;
                                font-weight: 700;
                            }
                        }

                        @media screen and (max-width: 600px) {

                            &:nth-of-type(1),
                            &:nth-of-type(2),
                            &:nth-of-type(3),
                            &:nth-of-type(4) {
                                display: none;
                            }
                        }

                        @media screen and (max-width: 800px) {

                            &:nth-of-type(2),
                            &:nth-of-type(3),
                            &:nth-of-type(4) {
                                display: none;
                            }
                        }

                        @media screen and (max-width: 1060px) {

                            &:nth-of-type(3),
                            &:nth-of-type(4) {
                                display: none;
                            }
                        }
                    }

                }
            }
        }
    }

    height: 10000px;
}